export const searchTextState=atom({
  key:"searchTextState", 
  default:"",
});

export const productFilterState=atom({
  key:"productFilterState", 
  default:"",
});

export const filteredProducts=selector({
  key:"filteredProducts",
  get:({get})=>{
    const products=get(productState);
    const searchText=get(searchTextState);
    const filterItem=get(productFilterState)
    let filteredProducts=[];
    filteredProducts=products.filter(product=>
    product.name.includes(searchText)).filter(product=>filterItem=="" ? true: product.type==filterItem)
    
    return filteredProducts ;
  },
})